<template>
    <div class="calculator-container">
        <div class="calculator-card">
            <h2>Battery life calculator</h2>
        
            <div class="input-section">
                <div class="capacity-section">
                    <div class="input-field">
                        <label>Battery capacity (mAh)</label>
                        <input type="number" v-model="batteryCapacity" @wheel="handleWheel" @keydown.up="handleKeyUp" @keydown.down="handleKeyDown" @input="validateInput" min="0" step="1"/>
                    </div>
                </div>          
    
                <!--  -->
                <div class="divider"></div>
                <!--      -->
                <div class="current-time-section">
                    <div class="input-group">
                        <div class="input-field">
                            <label>Execution Current(mA)</label>
                            <input type="number" v-model="runningCurrent" @wheel="handleWheel" @keydown.up="handleKeyUp" @keydown.down="handleKeyDown" @input="validateInput" min="0" step="1"/>
                        </div>
                        <div class="input-field">
                            <label>Execution time(time)</label>
                            <input type="number" v-model="runningTime" @wheel="handleWheel" @keydown.up="handleKeyUp" @keydown.down="handleKeyDown" @input="validateInput" min="0" step="1"/>
                        </div>
                    </div>
            
                    <div class="input-group">
                        <div class="input-field">
                            <label>Idle current(mA)</label>
                            <input type="number" v-model="idleCurrent" @wheel="handleWheel" @keydown.up="handleKeyUp" @keydown.down="handleKeyDown" @input="validateInput" min="0" step="1"/>
                        </div>
                        <div class="input-field">
                            <label>Idle time(time)</label>
                            <input type="number" v-model="idleTime" @wheel="handleWheel" @keydown.up="handleKeyUp" @keydown.down="handleKeyDown" @input="validateInput" min="0" step="1"/>
                        </div>
                    </div>
            
                    <div class="input-group">
                        <div class="input-field">
                            <label>Ready current(mA)</label>
                            <input type="number" v-model="standbyCurrent" @wheel="handleWheel" @keydown.up="handleKeyUp" @keydown.down="handleKeyDown" @input="validateInput" min="0" step="1"/>
                        </div>
                        <div class="input-field">
                            <label>Ready time(time)</label>
                            <input type="number" v-model="standbyTime" @wheel="handleWheel" @keydown.up="handleKeyUp" @keydown.down="handleKeyDown" @input="validateInput" min="0" step="1"/>
                        </div>
                    </div>
                </div> 

                <button class="calculate-btn" @click="calculate">
                    <span class="material-icons">calculate</span>
                    Calculation
                </button>
                
                <div class="chart-container" v-if="averageCurrent > 0">
                    <canvas ref="powerChart"></canvas>
                </div>
                
                <div class="results" v-if="averageCurrent > 0">
                    <div class="result-chip">
                        Average current consumption: {{ averageCurrent.toFixed(3) }} mA
                    </div>
                    <div class="result-chip">
                        Expected battery life: {{ batteryLife.toFixed(1)}} Hours({{ formattedBatteryLife }})
                    </div>
                </div>
            </div>
        </div>
    </div>
  </template>
  
<script>
import { Chart } from 'chart.js/auto'

export default {    
    data() {
        return {
            batteryCapacity: 0,
            runningCurrent: 0,
            runningTime: 0,
            idleCurrent: 0,
            idleTime: 0,
            standbyCurrent: 0,
            standbyTime: 0,
            averageCurrent: 0,
            batteryLife: 0,
            chart: null
        };
    },

computed: {
    formattedBatteryLife() {
        const days = Math.floor(this.batteryLife / 24);
        const hours = Math.floor(this.batteryLife % 24);
        
        if (days > 0) {
            return `${days}day ${hours}hour`;
        }
        return `${hours}hour`;
    }
},
methods: {
    initChart() {
        const ctx = this.$refs.powerChart;
        const totalPower = (this.runningCurrent * this.runningTime) + 
                            (this.idleCurrent * this.idleTime) + 
                            (this.standbyCurrent * this.standbyTime);
                            
        const runningPercentage = ((this.runningCurrent * this.runningTime) / totalPower * 100).toFixed(1);
        const idlePercentage = ((this.idleCurrent * this.idleTime) / totalPower * 100).toFixed(1);
        const standbyPercentage = ((this.standbyCurrent * this.standbyTime) / totalPower * 100).toFixed(1);

        this.chart = new Chart(ctx, {
            type: 'bar',
            data: {
            labels: ['Power Consumption'],
            datasets: [
                {
                label: `Running (${runningPercentage}%)`,
                data: [this.runningCurrent * this.runningTime],
                backgroundColor: 'rgba(255, 99, 132, 0.8)',
                borderWidth: 1
                },
                {
                label: `Idle (${idlePercentage}%)`,
                data: [this.idleCurrent * this.idleTime],
                backgroundColor: 'rgba(54, 162, 235, 0.8)',
                borderWidth: 1
                },
                {
                label: `Ready (${standbyPercentage}%)`,
                data: [this.standbyCurrent * this.standbyTime],
                backgroundColor: 'rgba(255, 206, 86, 0.8)',
                borderWidth: 1
                }
            ]
            },
            options: {
                indexAxis: 'y',
                responsive: true,
                scales: {
                    x: {
                        stacked: true,
                        title: {
                            display: true,
                            text: 'Power Consumption (mAh)'
                        }
                    },
                    y: {
                        stacked: true
                    }
                },
                plugins: {
                    legend: {
                        position: 'bottom'
                    },
                    tooltip: {
                        callbacks: {
                            label: function(context) {
                            const label = context.dataset.label || '';
                            const value = context.parsed.x || 0;
                            return `${label}: ${value.toFixed(1)} mAh`;
                            }
                        }
                    }
                }
            }
        });
    },

    updateChart() {
        const totalPower = (this.runningCurrent * this.runningTime) + 
                    (this.idleCurrent * this.idleTime) + 
                    (this.standbyCurrent * this.standbyTime);

        const runningPercentage = ((this.runningCurrent * this.runningTime) / totalPower * 100).toFixed(1);
        const idlePercentage = ((this.idleCurrent * this.idleTime) / totalPower * 100).toFixed(1);
        const standbyPercentage = ((this.standbyCurrent * this.standbyTime) / totalPower * 100).toFixed(1);

        this.chart.data.datasets[0].label = `Running (${runningPercentage}%)`;
        this.chart.data.datasets[1].label = `Idle (${idlePercentage}%)`;
        this.chart.data.datasets[2].label = `Ready (${standbyPercentage}%)`;
        
        this.chart.data.datasets[0].data = [this.runningCurrent * this.runningTime];
        this.chart.data.datasets[1].data = [this.idleCurrent * this.idleTime];
        this.chart.data.datasets[2].data = [this.standbyCurrent * this.standbyTime];
        
        this.chart.update();
    },  

    handleWheel(event) {
        if (event.target.matches(':hover')) {
            event.preventDefault();
            const delta = event.deltaY > 0 ? -1 : 1;
            const newValue = Math.max(0, Number(event.target.value) + delta);
            event.target.value = newValue;
            event.target.dispatchEvent(new Event('input'));
        }
    },
        
    handleKeyUp(event) {
        event.preventDefault();
        const newValue = Number(event.target.value) + 1;
        event.target.value = newValue;
        event.target.dispatchEvent(new Event('input'));
    },
        
    handleKeyDown(event) {
        event.preventDefault();
        const newValue = Math.max(0, Number(event.target.value) - 1);
        event.target.value = newValue;
        event.target.dispatchEvent(new Event('input'));
    },

    validateInput(event) {
        const value = Number(event.target.value);
        if (value < 0) {
            event.target.value = 0;
            event.target.dispatchEvent(new Event('input'));
        }
    },

    calculate() {
        const totalTime = this.runningTime + this.idleTime + this.standbyTime;
        const averageCurrent =
        (this.runningCurrent * this.runningTime +
            this.idleCurrent * this.idleTime +
            this.standbyCurrent * this.standbyTime) /
        totalTime;
        const batteryLife = this.batteryCapacity / averageCurrent;
        
        this.averageCurrent = averageCurrent;
        this.batteryLife = batteryLife;

        this.$nextTick(() => {
            if (this.chart) {
                this.chart.destroy(); //   
                this.chart = null;
            }
            this.initChart();
        });
    },        
},
};
</script>
  
